<template>
	<v-container>
		<v-app-bar app color="primary" dark height="50">
			<div class="d-flex align-center">
				<v-img
					alt="Pict Picker Logo"
					class="shrink mr-2"
					contain
					:src="require('@/assets/selectImage.png')"
					transition="scale-transition"
					width="40"
				/>
			</div>

			<h2
				class="main-header"
				style="color: white; margin-left: 5px; margin-top: 10px"
			>
				Pic Picker
			</h2>

			<v-spacer></v-spacer>
			<div style="margin: 10px; cursor: pointer" @click="userClick">
				{{ currentUser.id }}
			</div>
			<tooltip text="Your score">
				<v-chip :disabled="score === '...'" small :color="scoreColor">{{
					score === "..." ? "..." : score + "/20"
				}}</v-chip>
			</tooltip>
			<tooltip text="Your proposals">
				<v-badge color="green" :content="proposals" overlap>
					<v-icon dark style="margin: 5px; cursor: pointer"
						>mdi-image</v-icon
					>
				</v-badge>
			</tooltip>
			<tooltip text="Sellers on the race">
				<v-badge color="green" :content="sellersLength" overlap>
					<v-icon
						dark
						@click="scoreDialog = true"
						style="margin: 5px; cursor: pointer"
						>mdi-account-tie</v-icon
					>
				</v-badge>
			</tooltip>
			<tooltip text="Go back">
				<v-icon
					dark
					@click="goBack"
					style="margin: 5px; cursor: pointer"
					>mdi-keyboard-backspace</v-icon
				>
			</tooltip>
			<logout>
				<v-icon>mdi-logout</v-icon>
			</logout>
		</v-app-bar>
		<router-view />
		<v-dialog v-model="scoreDialog">
			<scores
				:sellers="
					currentRaceUserInfo ? currentRaceUserInfo.sellers : []
				"
				:myId="currentUser.id"
			/>
		</v-dialog>
	</v-container>
</template>

<script>
import Logout from "@/common/components/Logout.vue";
import db, { updateUser } from "@/common/services/firebaseUserService";
import { doc, onSnapshot } from "firebase/firestore";
import { mapActions, mapState } from "vuex";
import Tooltip from "@/common/components/Tooltip.vue";
import Scores from "@/common/components/Scores.vue";
export default {
	components: {
		Logout,
		Tooltip,
		Scores,
	},
	data() {
		return {
			scoreDialog: false,
		};
	},
	computed: {
		...mapState("common", ["currentUser"]),
		...mapState("seller", ["currentRaceUserInfo"]),
		showLogout() {
			return this.$router.currentRoute.fullPath === "/seller";
		},
		sellersLength() {
			return this.currentRaceUserInfo && this.currentRaceUserInfo.sellers
				? Object.entries(this.currentRaceUserInfo.sellers).length || "0"
				: "?";
		},
		proposals() {
			return this.currentRaceUserInfo && this.currentRaceUserInfo.sellers
				? (this.currentRaceUserInfo.sellers[this.currentUser.id] &&
						this.currentRaceUserInfo.sellers[this.currentUser.id]
							.proposals) ||
						"0"
				: "?";
		},
		score() {
			return this.currentRaceUserInfo && this.currentRaceUserInfo.sellers
				? this.currentRaceUserInfo.sellers[this.currentUser.id]
					? this.currentRaceUserInfo.sellers[this.currentUser.id]
							.score
					: "0"
				: "...";
		},
		scoreColor() {
			return this.score === "..."
				? "grey"
				: this.score > 10
				? "green"
				: this.score > 5
				? "yellow darken-3"
				: "red";
		},
		// ...mapState("user", ["currentRace"]),
	},
	mounted() {
		this.syncRace();
	},
	methods: {
		...mapActions("common", ["updateCurrentUser"]),
		...mapActions("seller", ["setCurrentRace"]),
		...mapActions("seller", ["setCurrentRaceUserInfo"]),
		goBack() {
			this.$router.back();
		},
		userClick() {
			if (this.$router.currentRoute.fullPath !== "/seller")
				this.$router.push("/seller");
		},
		syncRace() {
			// if(!this.currentUser || !this.currentUser.id)return;
			const user = doc(db, "/users", "" + this.currentUser.raceUser);
			onSnapshot(user, (sn) => {
				console.log("Current user change detected");
				console.log({ data: sn.data() });
				try {
					let u = sn.data();
					if (!u) return;
					let d = u.race;
					if (!d) {
						d = [];
					}
					if (
						u.raceDone &&
						this.currentUser &&
						!this.currentUser.raceDone
					) {
						let winner = u.raceDone.winner === this.currentUser.id;
						this.$toast[winner ? "success" : "error"](
							"Race is over. You " + (winner ? "won!!" : "loose")
						);
						this.updateCurrentUser({
							raceDone: u.raceDone,
						});
						updateUser(this.currentUser.id, {
							raceDone: u.raceDone,
						});
						this.$router.push("/race-done");
					}
					this.setCurrentRaceUserInfo(u);
				} catch (e) {
					this.$toast.error(e.message);
					this.setCurrentRace(null);
				}
			});
		},
	},
	watch: {
		score(curr, prev) {
			let dif = curr - prev;
			if (Number.isNaN(dif)) return;
			if (curr > prev) {
				this.$toast.success(`You've won ${dif} points`);
			}
		},
	},
};
</script>

<style></style>
